<template>
	<div class="recommend">
		<swiper :options="swiperOption">
	        <swiper-slide v-for="(item,index) in banners" :key="item">
	          <img :src="item.url" alt="">
	          <span v-text="item.label" 
	          :class="{bgBlue:item.backgroundColor=='blue',bgRed:item.backgroundColor=='#c62f2f'}">
	          </span>
	        </swiper-slide>
	        <div class="swiper-pagination" slot="pagination"></div>
    	</swiper>
    	<div class="entry">
  			<router-link to="#">
	    		<span class="entry-icon">
	  				<svg class="icon" aria-hidden="true"><use xlink:href="#icon-icon-test22"></use></svg>
	    		</span>
	    		<span class="entry-title">FM</span>
  			</router-link>
  			<router-link to="#">
	    		<span class="entry-icon">
	  				<svg class="icon" aria-hidden="true"><use xlink:href="#icon-signboard"></use></svg>
	  				<span v-text="today"></span>
	    		</span>
	    		<span class="entry-title"></span>
  			</router-link>
  			<router-link to="#">
	    		<span class="entry-icon">
	  				<svg class="icon" aria-hidden="true"><use xlink:href="#icon-hot"></use></svg>
	    		</span>
	    		<span class="entry-title"></span>
  			</router-link>
    	</div> 
    <div class="changeOrderList">
    	<div class="recommend-list" ref="recommendList">
    		<bar>
    			
    			<svg class="icon bar-arrow" aria-hidden="true" slot="bar-arrow"><use xlink:href="#icon-more"></use></svg>
    		</bar>
    		<list-cover v-for="item in recommendList" :key="item">
    			<img :src="item.imgURL" alt="" slot="cover-img"/>
    			<span slot="cover-right-icon" class="right-icon">
    				<svg class="icon" aria-hidden="true" slot="bar-arrow"><use xlink:href="#icon-icon-test15"></use></svg>
    				{{item.num | Chinese}}
    			</span>
    			<p slot="cover-title" v-text="item.title" class="cover-title"></p>
    		</list-cover>
    	</div>
    	<div class="exclusive" ref="exclusive">
    		<bar>
    			
    			<svg class="icon bar-arrow" aria-hidden="true" slot="bar-arrow"><use xlink:href="#icon-more"></use></svg>
    		</bar>
    		<list-cover v-for="item in exclusiveList" :key="item">
    			<img :src="item.imgURL" alt="" slot="cover-img"/>
    			<span slot="cover-left-icon" class="left-icon">
    				<svg class="icon" aria-hidden="true"><use xlink:href="#icon-viewlist"></use></svg>
    			</span>
    			<p slot="cover-title" v-text="item.title" class="cover-title"></p>
    		</list-cover>
    	</div>
    	<div class="latest-music" ref="latestMusic">
    		<bar>
    			
    			<svg class="icon bar-arrow" aria-hidden="true" slot="bar-arrow"><use xlink:href="#icon-more"></use></svg>
    		</bar>
    		<list-cover v-for="item in latestList" :key="item">
    			<img :src="item.imgURL" alt="" slot="cover-img"/>
    			<p slot="cover-title" v-text="item.title" class="cover-title"></p>
    			<p slot="cover-p" v-text="item.intro" class="cover-p"></p>
    		</list-cover>
    	</div>
    	<div class="mv" ref="mv">
    		<bar>
    			MV
    			<svg class="icon bar-arrow" aria-hidden="true" slot="bar-arrow"><use xlink:href="#icon-more"></use></svg>
    		</bar>
    		<list-cover v-for="item in mvList" :key="item">
    			<img :src="item.imgURL" alt="" slot="cover-img"/>
    			<span slot="cover-right-icon" class="right-icon">
    				<svg class="icon" aria-hidden="true"><use xlink:href="#icon-icon-test20"></use></svg>
    				{{item.num | Chinese}}
    			</span>
    			<p slot="cover-title" v-text="item.title" class="cover-title"></p>
    			<p slot="cover-p" v-text="item.intro" class="cover-p"></p>
    		</list-cover>
    	</div>
    	<div class="special-column" ref="specialColumn">
    		<bar>
    			
    			<svg class="icon bar-arrow" aria-hidden="true" slot="bar-arrow"><use xlink:href="#icon-more"></use></svg>
    		</bar>
    		<div class="column" v-for="item in columnList" :key="item">
    			<div class="column-left">
    				<span></span>
    				<p>{{item.title}}</p>
    				<span> {{item.num}}</span>
    			</div>
    			<div class="column-right">
    				<img :src="item.imgURL" alt="">
    			</div>
    		</div>
    	</div>
    	<div class="fm" ref="fm">
    		<bar>
    			
    			<svg class="icon bar-arrow" aria-hidden="true" slot="bar-arrow"><use xlink:href="#icon-more"></use></svg>
    		</bar>
    		<list-cover v-for="item in fmList" :key="item">
    			<img :src="item.imgURL" alt="" slot="cover-img"/>
    			<span slot="cover-left-icon" class="left-icon">
    				{{item.name}}
    			</span>
    			<span slot="cover-right-icon" class="right-icon">
    				<svg class="icon" aria-hidden="true"><use xlink:href="#icon-icon-test2"></use></svg>
    			</span>
    			<p slot="cover-title" v-text="item.title" class="cover-title"></p>
    		</list-cover>
    	</div>
    </div>		
    	<div class="bottom">
    		<p>~</p>
    		<router-link to="/changeOrder"></router-link>
    	</div>
	</div>
</template>
<script>
import {swiper,swiperSlide} from 'vue-awesome-swiper'
import bar from "../smallComponents/bar"
import listCover from "../smallComponents/listCover"

export default{
	name:'recommend',
	components:{swiper,swiperSlide,bar,listCover},
	methods:{
	},
	computed:{
		today(){
			let time = new Date();
			return time.getDate();
		},
	},
	mounted(){
		window.scrollTo(0,0);
		let recommendList = this.$refs.recommendList;
		let exclusive = this.$refs.exclusive;
		let latestMusic = this.$refs.latestMusic;
		let mv = this.$refs.mv;
		let specialColumn = this.$refs.specialColumn;
		let fm = this.$refs.fm;
		let cRI = this.$store.getters.getRecommendSongIndex;
		let cEI = this.$store.getters.getExclusiveIndex;
		let cLI = this.$store.getters.getLatestSongIndex;
		let cMI = this.$store.getters.getRecommendMVIndex;
		let cSI = this.$store.getters.getSpecialColumnIndex;
		let cFI = this.$store.getters.getPublicFMIndex;
		recommendList.setAttribute("style","order:"+cRI);
		exclusive.setAttribute("style","order:"+cEI);
		latestMusic.setAttribute("style","order:"+cLI);
		mv.setAttribute("style","order:"+cMI);
		specialColumn.setAttribute("style","order:"+cSI);
		fm.setAttribute("style","order:"+cFI);
	},
	filters:{
		Chinese:function(value){
	      if(!value){
	        return '0';
	      }
	      value = value+'';
	      if(value.length<5){
	        return value;
	      }else{
	        var c=value.split('');
	        c.splice(-4,4,'');
	        return c.join('');
	      }
    	}
	},
	data () {
    	return {
      		swiperOption:{
		        setWrapperSize :true,
		        pagination : '.swiper-pagination',
		        autoplay:3000,
		        paginationClickable :true,
		        mousewheelControl : true,
		        observeParents:true,
		      },
		    banners:[
		    	{
		    		url:'static/img/banner1.jpg',
		    		label:'',
		    		backgroundColor:'#c62f2f'
		    	},{
		    		url:'static/img/banner2.jpg',
		    		label:'',
		    		backgroundColor:'#c62f2f'
		    	},{
		    		url:'static/img/banner3.jpg',
		    		label:'',
		    		backgroundColor:'blue'
		    	},{
		    		url:'static/img/banner4.jpg',
		    		label:'',
		    		backgroundColor:'#c62f2f'
		    	},{
		    		url:'static/img/banner5.jpg',
		    		label:'',
		    		backgroundColor:'#c62f2f'
		    	},{
		    		url:'static/img/banner6.jpg',
		    		label:'',
		    		backgroundColor:'blue'
		    	},{
		    		url:'static/img/banner7.jpg',
		    		label:'',
		    		backgroundColor:'#c62f2f'
		    	},{
		    		url:'static/img/banner8.jpg',
		    		label:'',
		    		backgroundColor:'blue'
		    	},
		    ],
		    recommendList:[{
		    	imgURL:'static/img/banner1.jpg',
		    	num:12342,
		    	title:''
		    },{
		    	imgURL:'static/img/banner3.jpg',
		    	num:1234,
		    	title:'>>'
		    },{
		    	imgURL:'static/img/banner4.jpg',
		    	num:123242,
		    	title:''
		    },{
		    	imgURL:'static/img/banner5.jpg',
		    	num:122,
		    	title:'Storm Thorgerson'
		    },{
		    	imgURL:'static/img/banner6.jpg',
		    	num:1214344,
		    	title:'R&B'
		    },{
		    	imgURL:'static/img/banner7.jpg',
		    	num:3546342,
		    	title:'90Vol.7 No.1'
		    },],
		    exclusiveList:[{
		    	imgURL:'static/img/banner5.jpg',
		    	title:','
		    },{
		    	imgURL:'static/img/banner4.jpg',
		    	title:'fashion'
		    },{
		    	imgURL:'static/img/banner5.jpg',
		    	title:''
		    }],
		    latestList:[{
		    	imgURL:'static/img/banner1.jpg',
		    	title:'',
		    	intro:''
		    },{
		    	imgURL:'static/img/banner2.jpg',
		    	title:'',
		    	intro:''
		    },{
		    	imgURL:'static/img/banner3.jpg',
		    	title:'',
		    	intro:''
		    },{
		    	imgURL:'static/img/banner4.jpg',
		    	title:'',
		    	intro:'Semini'
		    },{
		    	imgURL:'static/img/banner5.jpg',
		    	title:'',
		    	intro:''
		    },{
		    	imgURL:'static/img/banner6.jpg',
		    	title:'',
		    	intro:'A-Lin'
		    }],
		    mvList:[{
		    	imgURL:'static/img/banner5.jpg',
		    	num:7564789,
		    	title:'',
		    	intro:''
		    },{
		    	imgURL:'static/img/banner6.jpg',
		    	num:7564789,
		    	title:'',
		    	intro:''
		    },{
		    	imgURL:'static/img/banner7.jpg',
		    	num:7564789,
		    	title:'',
		    	intro:''
		    },{
		    	imgURL:'static/img/banner8.jpg',
		    	num:7564789,
		    	title:'Living Out Loud',
		    	intro:'Sia'
		    }],
		    columnList:[{
		    	imgURL:'static/img/banner2.jpg',
		    	num:1234342,
		    	title:''
		    },{
		    	imgURL:'static/img/banner7.jpg',
		    	num:23234,
		    	title:''
		    },{
		    	imgURL:'static/img/banner6.jpg',
		    	num:123242,
		    	title:''
		    }],
		    fmList:[{
		    	imgURL:'static/img/banner6.jpg',
		    	title:'',
		    	name:''
		    },{
		    	imgURL:'static/img/banner5.jpg',
		    	title:'',
		    	name:''
		    },{
		    	imgURL:'static/img/banner4.jpg',
		    	title:'270',
		    	name:''
		    },{
		    	imgURL:'static/img/banner3.jpg',
		    	title:'',
		    	name:''
		    },{
		    	imgURL:'static/img/banner1.jpg',
		    	title:'',
		    	name:''
		    },{
		    	imgURL:'static/img/banner8.jpg',
		    	title:'',
		    	name:''
		    }],
    	}
  	},
}
</script>
<style lang="less" scoped>
@import "../../var.less";
.recommend{
	position: relative;
	top: 4.5rem;
	.entry{
		width: 100%;
		height: 5rem;
		padding: 0.4rem 0;
		display: flex;
		flex-flow: row nowrap;
		justify-content: center;
		align-items: center;
		border-bottom: 0.1rem solid #e4e8e7;
		a{
			display: flex;
			width: 33.3%;
			height: 100%;
			flex-flow: column nowrap;
			justify-content: space-around;
			align-items:center;
			.entry-icon{
				width: 2.4rem;
				height: 2.4rem;
				border-radius: 50%;
				border:0.05rem solid @mainColor;
				padding: 0.1rem;
				position: relative;
				svg{
					width: 1.3rem;
					height: 1.3rem;
					position: absolute;
					top: 50%;
					margin-top: -0.65rem;
					left: 50%;
					margin-left: -0.65rem;
					fill:@mainColor;
				}
				span{
					width: 1rem;
					height: 1rem;
					font-size: 0.3rem;
					color: @mainColor;
					position: absolute;
					top: 50%;
					margin-top: -0.5rem;
					left: 50%;
					margin-left: -0.5rem;
					text-align: center;
					line-height: 1.4rem;
				}
			}
			.entry-title{
				color: black;
				font-size: 0.5rem;
			}
		}
	}
	.changeOrderList{
		display: flex;
		flex-flow:column nowrap;

		.recommend-list{
		padding-top: 0.8rem;
		width: 100%;
		display: flex;
		flex-flow: row wrap;
		justify-content:space-between;
		.cover{
			width: 33%;
			margin-bottom: 1rem;
			img{
				height: 5rem;
			}
		}
	}
	.exclusive{
		display: flex;
		flex-flow:row wrap;
		justify-content:space-between;
		.cover{
			width: 49.4%;
			img{
				height: 5rem;
			}
			p{
				font-size: 0.5rem;
			}
			&:last-child{
				width: 100%;
				height: 9rem;
				margin-top: 0.3rem;
				img{
					height: 6rem;
				}
			}
		}
	}
	.latest-music{
		display: flex;
		flex-flow: row wrap;
		justify-content:space-between;
		.cover{
			width: 33%;
			margin-bottom: 1rem;
			img{
				height: 5rem;
			}
			.cover-title{
				height: 0.9rem;
			}
		}
	}
	.mv{
		margin-top: 1rem;
		display: flex;
		flex-flow: row wrap;
		justify-content:space-between;
		.cover{
			width: 49.5%;
			margin-bottom: 1rem;
			img{
				height: 5rem;
			}
			.cover-title{
				height: 0.9rem;
			}
		}
	}
	.special-column{
		.column{
			width: 100%;
			height: 4rem;
			display: flex;
			justify-content:space-between;
			border-top: 1px solid #e1e5e4;
			.column-left{
				flex:1;
				padding: 0.5rem 0.3rem;
				position: relative;
				span:first-child{
					width: 1.4rem;
					height: 0.6rem;
					line-height: 0.6rem;
					color: @mainColor;
					border:1px solid @mainColor;
					font-size: 0.05rem;
					float: left;
					text-align: center;
					margin-right: 0.3rem;
				}
				p{
					font-size: 0.5rem;
					height: 2.4rem;
					line-height: 0.6srem;
				}
				span:last-child{
					color: #848887;
					position: absolute;
					bottom: 0.4rem;
					font-size: 0.4rem;
				}
			}
			.column-right{
				margin-left: 1rem;
				width: 5.5rem;
				height: 100%;
				img{
					display: inline-block;
					width: 100%;
					height: 100%;
				}
			}
			&:last-child{
				border-bottom: 1px solid #e1e5e4;
			}
		}
	}
	.fm{
		margin-top: 1rem;
		display: flex;
		flex-flow: row wrap;
		justify-content:space-between;
		.cover{
			width: 33%;
			margin-bottom: 1rem;
			img{
				height: 5rem;
			}
			.left-icon{
				width: 70%;
				position: absolute;
				top: auto;
				bottom: 0.4rem;
				text-overflow:ellipsis;
				overflow:hidden;
				white-space:nowrap;
			}
			.right-icon{
				width: 30%;
				height: 1.5rem;
				top: auto;
				bottom: 0.2rem;
				right: 0;
				svg{
					width: 100%;
					height: 100%;
				}
			}
		}
	}
	}
	.bottom{
		width: 100%;
		display: flex;
		flex-direction:column;
		justify-content:center;
		align-items:center;
		height: 6rem;
		border-top: 1px solid #e1e5e4;
		p{
			font-size: 0.5rem;
			color: #848887;
		}
		a{
			display: inline-block;
			width: 4rem;
			height: 1.6rem;
			border:1px solid @mainColor;
			color: @mainColor;
			font-size: 0.4rem;
			border-radius: 1rem;
			text-align: center;
			line-height: 1.6rem;
			margin-top: 1rem;
		}
	}
}
.swiper-container{
	width: 100%;
  	height: 6rem;
}
.swiper-container .swiper-wrapper .swiper-slide{
	position: relative;
}
.swiper-container .swiper-wrapper .swiper-slide img{
  width: 100%;
  height: 100%;
}
.swiper-container .swiper-wrapper .swiper-slide span{
  width: 2.6rem;
  height: 1rem;
  line-height: 1rem;
  padding-left: 0.6rem;
  border-radius: 1rem;
  color: white;
  position: absolute;
  right: -0.6rem;
  bottom: 0.3rem;
  font-size: 0.5rem;
}
.bgBlue{
	background-color: blue;
}
.bgRed{
	background-color: #c62f2f;
}
</style>


